h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

* {
  border: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  cursor: pointer;
  text-decoration: none;
}

a:link, a:visited, a:hover {
  text-decoration: none;
}

ul, ul li {
  list-style: none;
}

button, label {
  cursor: pointer;
}

img, table {
  max-width: 100%;
}

@keyframes animation-move {
  0% {
    background-position: center;
  }

  100% {
    background-position: 100%;
  }
}

@keyframes animation-scale {
  0% {
    transform: scale(.9);
  }

  100% {
    transform: scale(1);
  }
}

.header {
  padding-top: 9px;
  position: relative;
}

@media (width >= 768px) {
  .header {
    padding-top: 15px;
  }
}

.header__top {
  z-index: 2;
  align-items: center;
  display: flex;
  position: relative;
}

.header__background {
  z-index: 1;
  background-image: url("header-background.65a5f39a.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 300px;
  animation: 1s linear animation-scale;
  position: absolute;
  top: 0;
}

@media (width >= 768px) {
  .header__background {
    width: 50%;
    height: 384px;
    right: 0;
  }
}

@media (width >= 1280px) {
  .header__background {
    width: 56.6%;
    height: 674px;
  }
}

.header__title {
  text-align: center;
  grid-column: 1 / -1;
  margin-bottom: 20px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

@media (width >= 768px) {
  .header__title {
    text-align: left;
    width: 50%;
    margin: 40px 0 38px;
    font-size: 54px;
    line-height: 64.8px;
  }
}

@media (width >= 1280px) {
  .header__title {
    max-width: 470px;
    margin: 119px 0 89px;
    font-size: 72px;
    line-height: 86.4px;
  }
}

.header__events {
  display: none;
}

@media (width >= 1280px) {
  .header__events {
    color: #687480;
    text-transform: uppercase;
    writing-mode: vertical-rl;
    grid-area: 3 / 1 / 4 / 2;
    align-items: baseline;
    gap: 20px;
    font-size: 14px;
    font-weight: 700;
    display: flex;
    position: relative;
    bottom: 14px;
    transform: rotate(180deg);
  }

  .header__events:before {
    content: "";
    background-color: #1a5a4c;
    width: 1px;
    height: 70px;
    display: block;
  }
}

.header__button {
  background-color: #fff;
  border: 1px solid #1a5a4c;
  grid-column: 1 / -1;
  margin-inline: auto;
}

@media (width >= 768px) {
  .header__button {
    grid-column: 1 / 4;
    margin-inline: 0;
  }
}

@media (width >= 1280px) {
  .header__button {
    grid-column: 3 / 6;
  }
}

.header__button--text {
  color: #0f0e08;
  text-transform: uppercase;
  text-align: center;
  flex: 1;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
}

@media (width >= 768px) {
  .header__button--text {
    line-height: 70px;
  }
}

.header__arrow {
  background-color: #1a5a4c;
  background-image: url("icon-arrow-white.2b16c202.svg");
  animation: 1s ease-in-out 2 animation-move;
}

.header__arrow:hover {
  background-color: #f14a27;
}

.header__content {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-top: 250px;
  display: grid;
  position: relative;
}

@media (width >= 768px) {
  .header__content {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .header__content {
    --columns: 12;
  }
}

@media (width >= 768px) {
  .header__content {
    margin: 0;
  }
}

