.page {
  scroll-behavior: smooth;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  line-height: 100%;
  overflow-x: hidden;
}

.page__body {
  background-color: #fff;
  overflow-x: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

