.gallery {
  text-align: center;
}

@media (width >= 1280px) {
  .gallery {
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
  }
}

.gallery__title {
  margin-block: 40px;
}

@media (width >= 768px) {
  .gallery__title {
    text-align: left;
    margin: 0 0 34px 48px;
  }
}

@media (width >= 1280px) {
  .gallery__title {
    margin: 0 0 55px 56px;
  }

  .gallery__slider {
    display: none;
  }
}

.gallery__slides {
  gap: 16px;
}

@media (width >= 768px) {
