    --columns: 12;
  }
}

.gallery__item {
  cursor: pointer;
}

.gallery__item--1 {
  grid-column: 1 / 5;
}

.gallery__item--2 {
  grid-column: 5 / span 8;
}

.gallery__item--3 {
  grid-column: 1 / 9;
}

.gallery__item--4 {
  grid-column: 9 / span 4;
}

.gallery__list .slide__image {
  transition: all .3s;
}

.gallery__list .slide__image:hover {
  border-radius: 1%;
  transform: scale(1.04);
}

.slider {
  width: 100%;
  overflow: hidden;
}

.slider__point {
  background-color: #e0e0e0;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  margin-top: 20px;
  margin-right: 22px;
  transition: background-color .3s;
  display: inline-block;
}

.slider__switch {
  display: none;
}

.slides {
  transition: transform .3s ease-in-out;
  display: flex;
}

.slide {
  width: 100%;
  min-width: 100%;
  transition: transform .3s ease-in-out;
  display: block;
}

@media (width >= 768px) {
  .slide {
    width: 48.5%;
    min-width: 48.5%;
  }
}

.slide__image {
  width: 100%;
  height: 100%;
  display: block;
}

.slide1:checked ~ .gallery__slides {
  transform: translateX(0%);
}

.slide2:checked ~ .gallery__slides {
  transform: translateX(-100%);
}

@media (width >= 768px) {
  .slide2:checked ~ .gallery__slides {
    transform: translateX(-40%);
  }
}

.slide3:checked ~ .gallery__slides {
  transform: translateX(-200%);
}

@media (width >= 768px) {
  .slide3:checked ~ .gallery__slides {
    transform: translateX(-90%);
  }
}

.slide4:checked ~ .gallery__slides {
  transform: translateX(-300%);
}

@media (width >= 768px) {
  .slide4:checked ~ .gallery__slides {
    transform: translateX(-120%);
  }
}

.slide1:checked ~ .slider__navigation .slider__point[for="slide1"], .slide2:checked ~ .slider__navigation .slider__point[for="slide2"], .slide3:checked ~ .slider__navigation .slider__point[for="slide3"], .slide4:checked ~ .slider__navigation .slider__point[for="slide4"] {
  background-color: #1a5a4c;
}

